<template>
    <div id="yachtOrder">
        <main class="col">
            <div class="col-12 div_middle">
                <h1 id="title"></h1>
            </div>
            <div class="col-12 div_middle">
                <table class="table table-hover" style="font-size:20px;text-align:left;">
                    <tr>
                        <td style="width:500px;">
                            <b>:</b>
                            <span v-if="!ifUpdate"> {{member.name}}</span>
                            <input v-if="ifUpdate" class="form-control col-8" type="text" v-model="user.name">
                        </td>
                        <td><b>:</b>
                            <span v-if="!ifUpdate"> {{member.sex == 1?'':''}}</span>
                            <div v-if="ifUpdate">
                                <label class="radio-inline">
                                    <input type="radio" name="sex" value="" v-model="user.sex">
                                </label>
                                <label class="radio-inline">
                                    <input type="radio" name="sex" value="" v-model="user.sex"
                                        style="margin-left:15px;">
                                </label>
                            </div>
                        </td>
                        <td></td>
                    </tr>
                    <tr>
                        <td><b>:</b>
                            <span v-if="!ifUpdate"> {{member.age}}</span>
                            <input v-if="ifUpdate" class="form-control col-8" type="number" max="150" min="0"
                                v-model="user.age" onkeydown="if(event.keyCode==32){return false;}">
                        </td>
                        <td><b>:</b>
                            <span v-if="!ifUpdate"> {{member.tel}}</span>
                            <input v-if="ifUpdate" class="form-control" type="tel" v-model="user.tel"
                                disabled="disabled">
                        </td>
                        <td style="text-align:right;">
                            <button class="btn btn-sm btn-info" @click="changeToUpdate" v-if="!ifUpdate"></button>
                            <button class="btn btn-sm btn-success" v-if="ifUpdate"
                                @click="updateMemberMsg"></button>
                            <button class="btn btn-sm btn-danger" data-toggle="modal"
                                data-target="#updatePwdModal"></button>
                        </td>
                    </tr>
                </table>
            </div>
            <hr>
            <div class="col-12 div_middle">
                <h1 id="title"></h1>
            </div>
            <div class="col-12" style="height:610px;">
                <table class="table"
                    style="font-size:13px;white-space:nowrap; text-align:center;vertical-align:middle; ">
                    <thead>
                        <tr>
                            <td> ()</td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                            <td></td>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-if="consumeList.length<=0">
                            <td colspan="9">
                                <h1 style="color:lightgrey;"></h1>
                            </td>
                        </tr>
                        <tr v-for="consume in consumeList" :key="consume.orderId">
                            <td><a :href="downLoadOrder+consume.orderId" title="">{{consume.orderId}}</a></td>
                            <td>{{consume.tel}}</td>
                            <td>{{consume.name}}</td>
                            <td>{{consume.yacht.name}}<a style="cursor:pointer;color:blue;" type="button"
                                    data-toggle="modal" data-target="#yachtDetailModal"
                                    @click="getYachtDetail(consume.yachtId)"> </a></td>
                            <td>
                                <span v-if="consume.yacht.state==1">{{consume.useTime|formatDate}}</span>
                                <span v-if="consume.yacht.state==2||consume.yacht.state==3"></span>
                            </td>
                            <td>
                                <span v-if="consume.yacht.state==1"></span>
                                <span v-if="consume.yacht.state==2||consume.yacht.state==3"></span>
                            </td>
                            <td>
                                <span v-if="consume.state==1" style="color:green"></span>
                                <span v-if="consume.state==0" style="color:red">
                                    <a class="btn btn-sm btn-success" v-if="consume.state==0" :href="toPay"></a>
                                </span>
                                <span v-if="consume.state==2" style="color:lightgrey"></span>
                            </td>
                            <td style="color:orange;font-size:15px;"><b>{{consume.price/100|NumFormat}}</b></td>
                            <td>{{consume.created|formatDate}}</td>
                            <td>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </main>

        <div id="yachtDetailModal" class="modal fade bs-example-modal-sm">
            <div class="modal-dialog modal-sm modal-dialog-centered" role="document">
                <div class="modal-content">
                    <div class="modal-body">
                        <p style="margin-top:13px;">
                            <b>:</b> {{yacht.id}}<br>
                            <b>:</b> {{yacht.name}}<br>
                            <b>:</b> {{yacht.color}}<br>
                            <b>:</b> {{yacht.length}}<br>
                            <b>:</b> {{yacht.members}}<br>
                            <b>:</b> {{yacht.address}}<br>
                            <span v-if="yachtScheme"><b>:</b> {{yachtScheme.time}}</span>
                        </p>
                    </div>
                </div>
            </div>
        </div>

        <!--  begin -->
        <div id="updatePwdModal" class="modal fade bs-example-modal-sm">
            <div class="modal-dialog modal-sm">
                <div class="modal-content">
                    <!--  -->
                    <div class="modal-header">
                        <h4 class="modal-title"></h4>
                        <button type="button" class="close" data-dismiss="modal">&times;</button>
                    </div>
                    <!--  -->
                    <div class="modal-body">
                        <div class="form-group">
                            <label>:</label>
                            <input type="password" class="form-control" v-model="oldPassword"
                                onkeydown="if(event.keyCode==32){return false;}">
                        </div>
                        <div class="form-group">
                            <label>:</label>
                            <input type="password" class="form-control" v-model="newPassword"
                                onkeydown="if(event.keyCode==32){return false;}">
                        </div>
                        <div class="form-group">
                            <label>:</label>
                            <input type="password" class="form-control" v-model="confirmPassword"
                                onkeydown="if(event.keyCode==32){return false;}">
                        </div>
                    </div>
                    <!--  -->
                    <div class="modal-footer">
                        <button type="button" class="btn btn-danger" @click="updatePassword"></button>
                    </div>
                </div>
            </div>
        </div>
        <!--  end -->
    </div>
</template>

<script>
    export default {
        name: 'yachtOrder',
        data() {
            return {
                user: {
                    id: " ",
                    tel: "",
                    name: "",
                    sex: "",
                    age: "",
                    level: ""
                },
                consume: {
                    orderId: "",
                    tel: "",
                    name: "",
                    yachtId: "",
                    schemeId: "",
                    useTime: "",
                    state: "",
                    yacht: "",
                    created: "",
                    yachtScheme: ""
                },
                yacht: {
                    id: "",
                    name: "",
                    color: "",
                    length: "",
                    members: "",
                    address: "",
                    state: "",
                    price: "",
                    yachtImgList: []
                },
                yachtScheme: {
                    id: "",
                    yachtId: "",
                    time: "",
                    price: ""
                },
                yachtSchemeList: [],
                selectScheme: "",
                selectUseTime: "",
                consumeList: [],
                ifUpdate: false,
                oldPassword: "",
                newPassword: "",
                confirmPassword: ""
            }
        },
        props: ['member'],
        methods: {
            getMemberConsumes() {
                this.axios({
                    url: "/api/consume/member/getMemberConsumes.action",
                    method: "get"
                }).then((response) => {
                    var map = response.data;
                    if (map.code == 401) { //
                        this.$emit("clearUser");
                        this.$router.push("/#/login");
                    } else if (map.code == 200) {
                        this.consumeList = JSON.parse(map.data);
                        this.consumeList = this.consumeList.reverse();
                    } else {
                        console.log(map);
                    }
                }).catch((error) => {
                    console.log(error);
                    if (error.response.status == 401) { //
                        window.location.href = '/#/login';
                        this.$emit("clearUser");
                    }
                });
            },
            getYachtDetail(yachtId) {
                var consume = this.consumeList.find(function (consume) {
                    return consume.yachtId === parseInt(yachtId);
                });
                this.yacht = consume.yacht;
                this.yachtScheme = consume.yachtScheme;
            },
            changeToUpdate() {
                this.user.id = this.member.id;
                this.user.name = this.member.name;
                this.user.age = this.member.age;
                this.user.sex = this.member.sex;
                this.user.tel = this.member.tel;
                if (this.user.sex == 0) {
                    this.user.sex = "";
                } else if (this.user.sex == 1) {
                    this.user.sex = "";
                }
                this.ifUpdate = true;
            },
            updateMemberMsg() { //
                if (!(/^[0-9]+$/.test(this.user.age))) {
                    this.$emit("showAlert", true, "");
                } else {
                    if (this.user.sex == "") {
                        this.user.sex = 0;
                    } else {
                        this.user.sex = 1;
                    }
                    this.axios({
                        url: "/api/member/updateMemberMsg.action",
                        method: "post",
                        data: this.user
                    }).then((response) => {
                        var map = response.data;
                        if (map.code == 200) {
                            if (map.data == null) {
                                this.$emit("showAlert", true, "");
                                this.$emit("getUser");
                                this.ifUpdate = false;
                            } else {
                                this.$emit("showAlert", true, map.data);
                            }
                        }
                    }).catch((error) => {
                        console.log(error);
                        this.$emit("showAlert", true, ",");
                    });
                }
            },
            updatePassword() { //
                if (this.newPassword.length < 6) {
                    this.$emit("showAlert", true, "");
                } else if (this.confirmPassword != this.newPassword) {
                    this.$emit("showAlert", true, "");
                } else {
                    this.axios({
                        url: "/api/member/member/updatePassword.action",
                        method: "post",
                        data: {
                            id: this.member.id,
                            oldPassword: this.oldPassword,
                            newPassword: this.newPassword
                        },
                        transformRequest: [function (data) { //post
                            // Do whatever you want to transform the data
                            let ret = '';
                            for (let it in data) {
                                ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) +
                                    '&';
                            }
                            return ret;
                        }],
                    }).then((response) => {
                        var map = response.data;
                        if (map.code == 401) { //
                            this.$router.push("/login");
                        } else if (map.code == 200) {
                            if (map.data == null) { //
                                this.clearPassword();
                                $("#updatePwdModal").modal("hide");
                                this.$emit("showAlert", true, "");
                            } else {
                                this.$emit("showAlert", true, map.data);
                            }
                        }
                    }).catch((error) => {
                        console.log(error);
                        if (error.response.status == 401) { //
                            window.location.href = '/#/admin';
                        }
                    });
                }
            },
            clearPassword() {
                this.oldPassword = "";
                this.newPassword = "";
                this.confirmPassword = "";
            }
        },
        created() {
            this.getMemberConsumes();
            $(".modal-backdrop").remove();
        },
        filters: {
            formatDate(dataStr) {
                var time = new Date(dataStr);

                function timeAdd0(str) {
                    if (str < 10) {
                        str = '0' + str;
                    }
                    return str
                }
                var y = time.getFullYear();
                var m = time.getMonth() + 1;
                var d = time.getDate();
                var h = time.getHours();
                var mm = time.getMinutes();
                var s = time.getSeconds();
                return y + '-' + timeAdd0(m) + '-' + timeAdd0(d) + ' ' + timeAdd0(h) + ':' + timeAdd0(mm);
            },
            NumFormat(value) {
                if (!value) return '0.00';

                /*Number(value).toFixed(0)0.511Nils*/
                var intPart = Number(value) | 0; //
                var intPartFormat = intPart.toString().replace(/(\d)(?=(?:\d{3})+$)/g, '$1,'); //

                var floatPart = ".00"; //
                value = value.toString();
                var value2Array = value.split(".");

                //=2
                if (value2Array.length == 2) {
                    floatPart = value2Array[1].toString(); //

                    if (floatPart.length == 1) { //0,
                        return intPartFormat + "." + floatPart + '0';
                    } else {
                        return intPartFormat + "." + floatPart;
                    }

                } else {
                    return intPartFormat + floatPart;
                }

            }
        }
    }
</script>

<style scoped>
    #title {
        color: deepskyblue;
        margin-top: 15px;
    }

    #yachtOrder {
        overflow-x: auto
    }
</style>